import { getCategoryList, getTopBooks } from './js/api-functions.js';
import {
  renderCategoryList,
  renderBookCardlist,
} from './js/render-functions.js';
import './js/hero.js';
import './js/feedbacks.js';
import './js/menu.js';
// import { getCategoryList } from './js/api-functions.js';
// import { renderCategoryList } from './js/render-functions.js';

async function initCategories() {
  try {
    const categories = await getCategoryList();
    // —Ç—É—Ç –º–æ–∂–Ω–∞ —Ä–µ–Ω–¥–µ—Ä–∏—Ç–∏ –∫–∞—Ç–µ–≥–æ—Ä—ñ—ó –≤ DOM
    renderCategoryList(categories);
  } catch (error) {
    console.error(error);
  }
}
initCategories();

async function initBooks() {
  try {
    const topbooks = await getTopBooks();

    // ‚úÖ –í–∏–∑–Ω–∞—á–∞—î–º–æ —à–∏—Ä–∏–Ω—É –µ–∫—Ä–∞–Ω–∞
    const screenWidth = window.innerWidth;

    // ‚úÖ –ó–∞–ª–µ–∂–Ω–æ –≤—ñ–¥ —Ä–æ–∑–º—ñ—Ä—É –µ–∫—Ä–∞–Ω–∞ ‚Äî –æ–±—Ä—ñ–∑–∞—î–º–æ –º–∞—Å–∏–≤
    const limitedBooks =
      screenWidth < 768 ? topbooks.slice(0, 10) : topbooks.slice(0, 24);

    renderBookCardlist(limitedBooks);
  } catch (error) {
    console.error('initBooks error:', error);
  }
}
initBooks();
